.step-card {
  background: var(--theme-primary-light);
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  display: block;
  text-decoration: none;
  margin-bottom: 20px;
  font-size: 18px;
}

.step-card:hover {
  background-color: var(--theme-secondary);
}

.step-card img {
  width: 50%;
  margin: auto;
}

.step-card h5 {
  color: var(--theme-primary);
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  .step-card img {
    width: 100%;
  }
  .step-card {
    text-align: left;
  }
}
