.dark-background-div {
  background-color: var(--theme-primary);
  color: white;
}

.dark-background-div p {
  color: #ffffffe0;
}

.light-background-div {
  background-color: var(--theme-primary-light);
  color: black;
}

.light-background-div p {
  color: #000000c2;
}

.loan-details-img {
  width: 90px;
}

.back-top-btn {
  text-decoration: none;
  margin-top: 20px;
  font-size: 17px;
}

.back-top-btn p {
  margin: 0;
}

.loan-details-hr {
  background: var(--theme-secondary);
  content: "";
  display: block;
  height: 4px !important;
  margin: 10px 0 30px 0;
  width: 100px;
  opacity: 1;
}
